import React from "react";
import { Card } from "./Card";


export const CardList = ({ robotFriends }) => {
  return (
    <div className="CardComponent">
      {robotFriends.map((user, i) => {
        return (
          <Card
            key={i}
            id={robotFriends[i].id}
            name={robotFriends[i].name}
            email={robotFriends[i].email}
          />
        );
      })}
    </div>
  );
};
